import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Grid, Card, Tabs, Tab } from '@material-ui/core';
import { Typography } from '@material-ui/core';
import PropTypes from 'prop-types';

// import { createDemoCustomer } from '../../../store/actions/authActions';
import RegisterLive from './live-form';
import LoginForm from './login-form';
import { withNamespaces } from 'react-i18next';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      className="p-3"
      component="div"
      role="tabpanel"
      hidden={value !== index}
      {...other}>
      {value === index && <div>{children}</div>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired
};

export const Login = (props) => {
  const urlParams = new URLSearchParams(window.location.search);
  const referral = urlParams.get('ref');
  const utmCampaign = urlParams.get('utm-campaign');
  const [value, setValue] = useState(0);
  const handleChange = (event, newValue) => {
    window.parent.postMessage({ tab: newValue }, '*');
    setValue(newValue);
  };

  // useEffect(() => {
  //   if ('geolocation' in navigator) {
  //     navigator.geolocation.getCurrentPosition(function (position) {});
  //     navigator.geolocation.getCurrentPosition(function (position) {});
  //   }
  // }, []);
  const t = props.t;
  return (
    <>
      <Grid container>
        <Grid item className={"w-100"}>
          <div className="registration-widget py-4">
            <Card elevation={2} className={"pb-4"}>
              <div className="px-4 py-4">
                <Tabs
                  className="nav-tabs-second"
                  centered
                  value={value}
                  onChange={handleChange}>
                  <Tab label={t('Register')} />
                  <Tab label={t('Log In')} />
                </Tabs>
              </div>

              <TabPanel value={value} index={0}>
                <RegisterLive {...props} referral={referral} utmCampaign={utmCampaign} />
              </TabPanel>
              <TabPanel value={value} index={1}>
                <LoginForm {...props} />
              </TabPanel>
            </Card>
          </div>
        </Grid>
      </Grid>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    loading: state.apiCallsInProgress > 0
  };
};

const mapDispatchToProps = {
  //loginUser: createDemoCustomer
};

export default withNamespaces()(connect(mapStateToProps, mapDispatchToProps)(Login));
